<template>
    <div class="new-address">
        <van-nav-bar title="新增地址" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <van-address-edit
            :area-list="areaList"
            show-postal
            show-delete
            show-set-default
            :search-result="searchResult"
            :area-columns-placeholder="['请选择', '请选择', '请选择']"
            @save="onSave"
        />
    </div>
</template>

<script>
    import { Toast } from "vant";
    // 引入地址数据信息
    import areaList from "../assets/js/address";

    export default {
        data() {
            return {
                areaList,
                searchResult: [],
            };
        },

        methods: {
            
            // 新增地址
            onSave({ name, tel, province, city, county, postalCode, areaCode,addressDetail, isDefault }) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                let data = {
                    appkey: this.appkey,
                    tokenString,
                    name, // 姓名
                    tel, // 电话
                    province, // 省份
                    city, // 城市
                    county, // 区县
                    postalCode, // 邮政编码
                    areaCode, // 地区编号
                    addressDetail, // 详细地址
                    isDefault: Number(isDefault), // 默认地址
                }

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/addAddress",
                    data,
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == 9000) {

                        // this.$router.push({ name: "Address" });
                        this.$router.go(-1);

                    } else {
                        Toast(res.data.msg);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });

            },

        },
    }
</script>

<style lang="less" scoped>

</style>